.mainContainer {
  /* margin-top: 10rem; */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 10rem;
  margin: 0 1rem;
  text-align-last: center;
}

.mainContainer > button {
  border-radius: 4px;
  border: 1px solid pink;
  background-color: tan;
  padding: 1rem;
  font-size: 1rem;
  cursor: pointer;
}
.mainContainer > button:hover {
  transform: scale(1.1);
}
